export default{
    props: {
    	id:{
    		type:String
    	},
    	isRouter:{
    		type:Boolean,
    		default:false
    	}
    },
    computed: {
       isActive(){
           if(this.$parent.value===this.id){
               return true;
           }
       }
    },
    methods:{
    	goToRouter(){
    		this.$parent.$emit('input',this.id)
    		if(this.isRouter){
    			this.$router.push(this.id)
    		}
    	}
    }
}



